import { Button, Link, makeStyles} from "@material-ui/core"
import { useEthers } from "@usedapp/core"


const useStyles = makeStyles((theme) => ({
  container: {
    padding: theme.spacing(4),
    display: "flex",
    justifyContent: "flex-end",
    gap: theme.spacing(1)
  },
}))

export const Header = () => {
  const classes = useStyles()

  const { account, activateBrowserWallet, deactivate } = useEthers()

  const isConnected = account !== undefined

  return (
    <div className={classes.container}>
      <Button color="primary" variant="contained">
      <a  className="buttonD" href="https://forgetoken.org" target="_blank" rel="noopener noreferrer">Home</a>
      </Button>
      <Button color="primary" variant="contained">
      <a  className="buttonD" href="https://quickswap.exchange/#/swap?outputCurrency=0xF44fB43066F7ECC91058E3A614Fb8A15A2735276" target="_blank" rel="noopener noreferrer">Buy Forge</a>
      </Button>
      <Button color="primary" variant="contained">
      <a  className="buttonD" href="https://forgetoken.org/whitepaper.html" target="_blank" rel="noopener noreferrer">Info</a>
      </Button>
      <Button color="primary" variant="contained">
      <a  className="buttonD" href="https://forgetoken.org/stats.html" target="_blank" rel="noopener noreferrer">Stats</a>
      </Button>
      {isConnected ? (
        <>
          <Button color="primary" variant="contained">
            {`${account?.slice(0, 4)}...${account?.slice(-3)}`}
          </Button>
          <Button variant="contained" onClick={deactivate}>
            Disconnect
          </Button>
        </>
      ) : (
        <Button
          className="buttonW"
          color="primary"
          size="large"
          variant="contained"
          onClick={() => activateBrowserWallet()}
        >
          Connect
        </Button>
      )}
    </div>
  )
}